*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Raleway", sans-serif;
}
#content3{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    background-color: rgb(223, 205, 205);
    padding: 30px;
}

.content3 {
    opacity: 0.9;
}
.sub-content3{
    position: relative;
    bottom: 30px;
    text-align: center;
}
.sub-content3{
    margin: 30px 0px;
}

.sub-content3 h1{
    margin: 10px 0px;
    opacity: 0.8;
}
.sub-content3 h3{
    margin: 10px 0px;
    opacity: 0.8;
}

.sub-content3 p{
    
}


.rowed{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    
}
.carder{
    flex: 1 1 20rem ;
    display: flex;
    margin: 10px 15px;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: #fff;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.4);
    padding: 10px 20px;
    border-radius: 15px;
    width: 300px;
    max-width: 100%;
    height: 300px;
    transition: all 10s ease;
}
.carder h4{
    
   width: 100%;
   text-align: center;
   line-height: 30px;
   opacity: 0.8;
}


@media screen and (max-width: 350px) {
    .carder h4{
        line-height: 20px;
        overflow: hidden;
    }
  }

